import axios, { AxiosResponse } from 'axios';
import {
  unAuthIntance,
  authIntance,
  storageAuthKey,
} from './instance';
import { AuthForm } from 'types/common';
import qs from 'qs';
import { IUser } from 'types/user';

export const authApi = {
  register: async (data: AuthForm) => {
    return await unAuthIntance.post('/register', data).then((res) => {
      // convertStorage();
      // window.location.reload();
      convertStorage();
      // return res;
      return new Promise<AxiosResponse<IUser>>((resolve) => {
        setTimeout(() => {
          window.location.reload();
          resolve(res);
        }, 1000);
      });
    });
  },
  login: async (data: AuthForm) =>
    await unAuthIntance.post('/login', data),
  logout: async () => {
    window.localStorage.removeItem(storageAuthKey);
  },
  getUserData: async () => {
    try {
      return await authIntance.get(`/me`);
    } catch (error) {
      window.localStorage.removeItem(storageAuthKey);
      return Promise.reject(error);
    }
  },
};

